import type { Controller } from '@ali/4ever-cangjie';
import hasInlineCodeMark from './hasInlineCodeMark';

export default function hasInlineOrInlineCodeMark(controller: Controller) {
  const { selection, document, inlines } = controller.value;
  if (selection.isCollapsed) {
    return false;
  }

  const inlineCodeMark = hasInlineCodeMark(document, selection);
  return inlines.length > 0 || inlineCodeMark;
}